.ProductCard {
  padding: 30px 60px;
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  background-color: rgb(239, 239, 239);
  border-radius: 20px;
  margin: auto;
}

.ProductCard-title {
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
}

.ProductCard-image {
  height: 200px;
  width: 265px;
  border-radius: 50px;
  border-bottom-right-radius: 0;
  border: 3px solid white;
}

.ProductCard-price {
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 2rem;
  font-weight: bold;
}

.ProductCard-priceDetails {
  font-size: 1rem;
  font-weight: normal;
}

.ProductCard-buttons {
  display: flex;
  gap: 10px;
}

.ProductCard-button {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  font-weight: bold;
}

.ProductCard-button.wide {
  width: 120px;
  border-radius: 20px;
}
