.sum-container, .payment-method-container {
    margin: 200px auto auto;
    border-radius: 20px;
    background-color: #e3e3e3;
    padding: 50px;
    width: 80%;
}
.sum-container p {
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
}
.payment-method-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
}
.payment-method-container a {
    text-align: center;
    margin: auto;
    padding: 17px;
    width: 59%;
    font-size: 1.5rem;
    
    background-color: yellowgreen;
    transition: transform .25s;
    cursor: default;
    border-radius: 5px;
}
.payment-method-container a:nth-child(2) {
    margin-top: 50px;
    background-color: #212C62;
    color: #fff;
}
.payment-method-container a:nth-child(3) {
    margin-top: 50px;
    background-color: #fa7a7a;
    color: #fff;
}
.payment-method-container a:active {
    transform: scale(.8);
}
.paypal-button-container {
    margin-top: 25px; 
    margin-left: 230px;
    
  }