.Shop {
  width: 70%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: auto;
  justify-content: space-evenly;

}
.Shop .ProductCard {
  margin: 30px;
}

.Shop--loading {
  flex: 1;
  display: grid;
  place-items: center;
  font-size: 2rem;
}

.Shop-cartButton {
  position: fixed;
  top: 80px;
  right: 80px;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background-color: rgb(255, 255, 255);
  display: grid;
  place-items: center;
  border: 2px solid grey;
}

.Shop-cartButton img {
  height: 50%;
}
